@import url(https://fonts.googleapis.com/css?family=Lato:200, 400);
@import url(https://fonts.googleapis.com/css?family=Lustria);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Lustria";
  font-weight: 100;
  color: #222;
}

.App {
    padding-top: 20px;
    padding-bottom: 20px;
    max-width: 960px;
    margin: 0 auto;
  }
  /*
  .App__header {
    padding-left: 20px;
    padding-right: 20px;
  }*/
  
  .App__main {
    margin-top: 20px;
  }
  
.Header {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    background-color: rgb(128, 126, 126);
}

.Header a {
    color: white;
    text-decoration: none;
}
.Header__not-logged-in {
    width: 30%;
    display: flex;
    justify-content: space-between;
    /*flex: 1;
    align-items: center;
    justify-content: flex-end;*/
}
.ListItem{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;
}
.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;
}
.list header {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
header a {
    color: black;
}
.ListItem a {
    color: blue;
}
.poster {
    display: flex;
    align-items: center;
    background-color: teal;
    width: 100px;
    height:150px;
    color: white;
}

.movie_details, .right li, .review-details{
    display: flex;

}

.movie_details{
    width: 80%;
    margin: 20px auto;
}
.left {
    width:40%;
    margin-right: 20px;
    text-align: center;
    color: white;
}
.right {
    width: 60%
}
.movie_trailer,.movie_reviews {
    width: 80%;
    padding: 10px;
    margin: 10px auto;
}
.movie_trailer {
    background-color: chocolate;
}
a {
    text-decoration: none;
    color: white;
}
.movie_poster {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 200px;
    background-color: black;   
}
ul {
    list-style: none;
    padding-left: 0;
}

.right li span {
    width: 40%;
}

.review-icon {
    font-size: 12pt;
    vertical-align: super !important;
    width: 10%;
}
.review-text {
    width: 65%;
    margin: 0 10px 0 30px;
}

.form{
    max-width: 375px;
    margin: 0 auto;
}

input{
    margin: 10px 0;
}

.user_name input, .password input, .nick_name input {
    width: 80%;
}
.full_name input {
    width: 140px;
}

.full_name {
    width: 80%;
    display: flex;
    justify-content: space-between;
}

input[type='submit'] {
    margin-top: 20px;
    padding: 10px;
    border: none;
    background-color: black;
    color: white ;
}
input[type='submit']:hover {
    cursor: pointer;
}

.ReviewForm select, .ReviewForm label {
    font-size: 1.1em;
}
  
.ReviewForm label {
    display: inline-block;
    margin: 10px 10px 20px;
    margin-left: 0;
}
  

